.items{
    display: flex;
    flex-direction: column;
    margin: 15px;
    padding: 10px;
    color: black;
    border-radius: 10px;
}
.items:hover{
    opacity: 2.5;
    cursor: pointer;
    color: red;
    background-color: #df9f9f80;
}
.items-image{
    
    background-size:100% 100%;
    height:140px;
    width: 190px;
    border-radius: 15px;
}
.item1{
    background-image: url("../../assests/بیمه.jpg") ;
}
.item2{
    background-image: url("../../assests/حضوروغیاب.jpg") ;
}
.item3{
    background-image: url("../../assests/fish.jfif") ;
}
.item4{
    background-image: url("../../assests/sap.png") ;
}
.items-label{
    display: flex;
    justify-content: center;
    padding: 3px;    
}
.back-white{
    background-color: rgb(255, 255, 255);
    margin: 3%;
    padding: 3%;
    width: 85%;
    height: 80%;
    border-radius: 10px;
    border: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    overflow-y: auto;

}
.back-blue{
    background-color: rgb(52, 52, 189);
    padding: 0%;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}