
.disp{
	position: fixed;
	width: 60%;
	min-width: 350px;
	top:100px;
	background-color: #fff;
	box-shadow: 0px 0px 20px gray;
	min-height: 500px;
	height: 80%;
	opacity: 0.9;
	
}
.disp::before{
	opacity: 1;
}
.menubar{
    position: fixed;
    background-color: rgb(98, 182, 234);
    top:36px;
    right: 0px;
    z-index: 1;
	height: 99vh;
	margin-left: 0;
	padding: 5px;
	font-size: 0;
}

.icons{
	margin: 0;
	display: flex;
	justify-content: center;
	flex-direction: column;
	padding-top:5px;
	font-size: inherit;
}
.menubar:hover{
	font-size:12px;
	transition-property: all;
	transition-duration: 1s;
}


.toolbar{
	position: fixed;
	top:0;
	right: 0;
	width: 100%;
	height: 50px;
	background:linear-gradient(90deg,rgb(255, 255, 255) , rgb(87, 135, 190) 20%) ;
	display: flex;
	justify-content: space-between;
	box-shadow:5px 5px 10px 2px rgba(23, 12, 237, 0.2);
}
.homepage{
	color: #fff;
	font-size: 12px;
	font-family:vazir;
	margin: 5px;
}
.logoprof{
	display: flex;
	color: #fff;

}
.logo{
	margin-right: 40px;
}
.prof{
	padding-right: 5px;
	border-radius: 30px;
}
.prof2{
	margin-right: 5px;
	border-radius: 30px;
	display: none;
}
.proflabel{
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: small;
	padding-bottom: 4px;
	padding-right: 5px;
}
.prof:hover{
	cursor: pointer;
}
.prof:hover ~ .proflabel{
	color: black;
	cursor: pointer;
}
.proflabel:hover{
	color: black;
	cursor: pointer;
}
.proflabel2{
	display: flex;
	justify-content: center;
	align-items: center;
	font-size:large;
	margin: 15px;
}
.proflabel:hover~.profileinf,.prof:hover~.profileinf{
	visibility: visible;
	display: flex;
}
.profbtn{
	font-family: 'vazir';
	background-color: #4b69a8;
	padding: 3px;
	margin: 5px;
	border: none;
	border-radius: 10px;
	width: 75%;
	color: #fff;
}
.profbtn:hover{
	background-color: #fff;
	color: #4b69a8;
	cursor: pointer;
}
.profileinf{
	background:linear-gradient(180deg,rgba(104, 94, 156, 0.66) ,rgba(48, 155, 62, 0.692)) ;
	width: 180px;
	height: 250px;
	position: relative;
	top:30px;
	left: -90px;
	border-radius: 20px;
	padding: 10px;
	display: flex;
	z-index: 3000;
	align-items: center;
	flex-direction: column;
	visibility: hidden;
	display: none;
}
.profileinf:hover{
	visibility: visible;
	display: flex;
}
.homepages{
	display: flex;
	justify-content: left;
	color: #fff;
	font-size: small;
	direction: rtl;
	align-items: center;
	
}
.homepagex{
	margin-left: 10px;
	padding-bottom: 4px;
	background-color: #ffffff20;
	border-radius: 5px;
	padding: 5px;
	text-decoration: none;
	color: white;
}
.homepagex:hover{
	color: black;
	cursor: pointer;
	
}
@media(max-width: 800px){
	.menubar{
		position: fixed;
		background-color: rgb(90, 165, 211);
		top:36px;
		right:0px;
		z-index: 1;
		border-radius:0px;
		margin-left: 0;
		font-size:9px;
		padding: 5px;
	}
	.homepage{
		color: #fff;
		font-size: 9px;
		font-family:'vazir';
		margin: 3px;
	}
	.logoprof{
		display: flex;
		color: #fff;
	
	}
	.icons{
		font-size: 9px;
	}
	.icons2{
		display: none;
	}
	.logo{
		margin-right: 10px;
	}
	.prof{
		margin-right: 5px;
		border-radius: 30px;
	}
	.prof2{
		margin-right: 5px;
		border-radius: 30px;
		display: inline;
	}
	.prof2:hover{
		border: solid 1px white;
	}
	.proflabel{
		display: flex;
		justify-content: center;
		align-items: center;
		font-size:9px;
		padding-bottom: 4px;
	}
	.homepages{
		display: flex;
		justify-content: left;
		color: #fff;
		font-size: 9px;
		direction: rtl;
		align-items: center;
	}
	.homepagex{
		margin-right: 4px;
		padding-bottom: 4px;
		font-size:9px;
		margin-left: 0px;
	}
	.homepage:hover{
		color: black;
		cursor: pointer;
		
	}
	.disp{
		position: fixed;
		width: 80%;
		min-width: 350px;
		top:100px;
		background-color: #fff;
		box-shadow: 0px 0px 20px gray;
		min-height: 500px;
		height: 80%;
		opacity: 0.9;
		
	}

}

