.icon{
    width: 35px;
    height: 36px;
    background-repeat: no-repeat;
    background-size:25px 30px;
    background-position: center;
    padding:5px;
    background-color: #fff;
    border-radius: 50%;
    margin: 3px;
}
.iconinfo{
    height: 36px;
    text-align: center;
    direction: rtl;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    padding-bottom: 3px;
    font-size: inherit;
    margin-top: 5px;
}

.linktoicons{
    background-color: transparent;
    margin-top: 4px;
    margin-left: 0px;
    border-radius: 20px;
    display: flex;
    flex-direction:row-reverse;
    text-decoration: none;
    font-family: 'vazir';
    color: black;
    cursor: pointer;
    border: solid 1px rgba(253, 253, 253, 0.635);
    opacity: 0.8;
    padding: 3px;
}
.linktoicons:hover,.linktoicons:active{
    border: solid 1px rgb(255, 255, 255);
    color: rgb(255, 255, 255);
    opacity: 1;
}
@media(max-width: 600px){
    .icon{
        width: 20px;
        height: 21px;
        background-repeat: no-repeat;
        background-size:20px 21px;
        background-position: center;
        padding:5px;
        background-color: #fff;
        border-radius: 50%;
        margin: 3px;
    }
    .iconinfo{
        height: 21px;
        text-align: center;
        direction: rtl;
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        padding-bottom: 3px;
        font-size: inherit;
        margin-top:5px;
    }
    .linktoicons{
        background-color: transparent;
        margin-top: 4px;
        margin-left: 0px;
        border-radius:10px;
        display: flex;
        flex-direction:row-reverse;
        text-decoration: none;
        font-family: 'vazir';
        color: black;
        cursor: pointer;
        border: solid 1px rgba(253, 253, 253, 0.635);
        opacity: 0.8;
        padding: 3px;
    }
    .linktoicons:hover,.linktoicons:active{
        border: solid 1px rgb(255, 255, 255);
        color: rgb(255, 255, 255);
        opacity: 1;
    }

}