.slclabelreciver{
    font-size: medium;
}
.selectedOptionsList{
    margin: 2px;
    background-color: #6eac6e;
    min-width:45px;
    min-height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    padding: 5px 10px;
    font-size: small;

}
.slectedlist{
    display: flex;
    flex-wrap: wrap;
}

.slc12{
    border-radius: 5px;
    background:transparent;
    padding:5px;
    font-family: 'vazir';
    font-size: 10px;
    color:#892020;	
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;
    border: 1px solid;
    border-radius: 2px;
    margin: 10px;
    min-height: 20px;
    font-size: medium;
    direction: rtl;
}

.slc:focus,slc:valid{
    border:none;	
	outline:none;
	border:solid 2px rgb(57, 73, 183);	
}

.slcrecivers{
    display: block;
    position: absolute;
    z-index: 100;
    min-width:px;
    min-height: 200px;
    height: 50%;
    width: 70%;
    background-color: rgba(80, 150, 180, 0.9);
    top:50%    ;
    left:50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    padding: 8px;
}
.searvhbar{
    font-size: medium;
    display: block;
    background-color:#fff;
    border:none;
    border-radius: 5px;
    margin:8px;
    padding: 5px 10px;
    width: 90%;
    direction: rtl;
    font-family:'vazir';
    
}
.optreciver{
    height: 70%;
    overflow:auto;
}
.recivers{
        
    font-size:medium;
    display: block;
    background-color:#fff;
    border: none;
    border-radius: 5px;
    margin:10px 5px;
    padding: 5px 10px;
    width: 90%;
    color: black;
    direction: rtl;
    cursor: pointer;
}
.slcbtn{
    display: flex;
    justify-content: center;
    
}
.sbtn{
    font-size: 15px;
    background-color: #6eac6e;
    min-width: 70px;
    min-height: 30px;
    border-radius: 5px;
    margin: 10px auto;
    border: none;
}
.sbtn:hover{
    cursor: pointer;
    background-color: #46ac46;
}
@media(max-width: 600px){
    .sbtn,.slclabelreciver,.recivers,.searvhbar,.slc12,.selectedOptionsList{
        font-size: 9px;
    }
}