
.dispMsg{
    overflow: Auto;
    margin: 10px;
    padding: 10px;
    height: 350px;
}
.disp2{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.messagestype{
    margin: 5px;
    background-color:rgb(255, 255, 255) ;
    direction: rtl;
    margin-bottom:20px; 
    display: flex;
}
.listmesage{
    margin-bottom:5px;
    background-color :rgba(139, 143, 146,0.5) ;
    cursor: pointer;
    padding: 3px;
    display: flex;
    flex-wrap: wrap;
}
.listmesage:hover{
    background-color: rgb(79, 80, 83);

}
.mesagetype{
    background-color:rgba(139, 143, 146,0.5);
    border-radius: 2px;
    height:15px ;
    font-size:13px ;
    margin:4px 4px ;
    padding:8px;
    display: flex;
    justify-content: center;
    align-items: center;  
}
.mesagetype2{
   background-color :rgb(255, 255, 255);
   border-radius: 2px;
   margin:4px ;
   height: 18px;
   padding:3px;
   cursor: pointer;
   font-size: small;
}
.mesagetype3{
    flex-grow: 1;
}
.mesagetype:hover{
    background-color: rgb(79, 80, 83);
    color: white;
    
}
.mesagetype5{
    width: 3%;
    min-width: 12px;
}
.mesagetype4{
    width: 25%;
    min-width:200px;
}
.mesagetype3{
    width: 50%;
    min-width:200px;
}
.mesagetype6{
    background-color: rgb(79, 80, 83);
    color: white;

}
.btnMsg{
    font-family: 'vazir';
    font-size: 13px;
    padding: 4px;
    min-width: 40px;
    margin: 2px 5px;
    background-color: rgba(255, 255, 255, 0.648);
    border: none;
    border-radius: 3px;
    cursor: pointer;
}
.btnMsg:hover{
    color: aliceblue;
    background-color: rgba(113, 213, 14, 0.854)
}
@media(max-width: 600px){
    .btnMsg,.mesagetype2,.mesagetype,.mesagetype6{
        font-size: 8px
    }
}