
.dispgroupsetting{
    margin: 5px;
    padding: 5px;
    background-color: #ffffff80;
    border-radius: 10px;
    border-top-left-radius:0px;
    border-top-right-radius:0px;
    height: 80%;
    
    
}
.contonerinput{
    margin: 5px;
    padding: 5px;
    font-size: 11px;
    font-family: 'vazir';
}
.contonerinput input{
    margin-right: 5px;
    font-family: 'vazir';
    font-size: medium;
    width: 150px;
}
.contonerinput label{
    margin-right: 5px;
    font-family: 'vazir';
    font-size: medium;
    width:150px;
    display: inline-block;

}

.contourlist{
    width: 95%;
    min-height:250px;
    height:75%;
    margin-top: 6px;
    padding: 10px;
    margin: 10px;
    background-color: white;
    border-radius: 5px;
    overflow-y: auto;
}
.contourlistchild{
    margin: 3px 10px;
    border-bottom: solid 1px rgba(21, 24, 26, 0.509);
    display: flex;
    justify-content: space-around;
}
.contourlistchild2{
    margin: 3px;
    background-color: rgba(184, 66, 66, 0);
    padding: 4px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    width: 20%;
    height: 15px;
    justify-content: center;
    font-size: 10px;
    overflow: hidden;
    max-width: 600px;
    font-weight: bold;
    
}
.contourlistchild3{
    background-color: rgba(184, 66, 66, 0.324);
    width: 8%;    
}
.contourlistchild4{
    background-color: rgba(160, 152, 152, 0.546);
    width: 25%;    
}
.contourlistchild5{
    background-color: rgba(160, 152, 152, 0.546);
    width: 20%;    
}
.contourlistchild6{
    width: 40%;
    background-color: green;
    color: white;
    padding: 3px;
    border: none;
    border-radius: 3px;
    font-family: 'vazir';
    font-size: smaller;

}
.contourlistchild6:hover{
    background-color: white;
    color: green;
    border: solid 1px green;
    
}
