.dispconnect{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    margin: 0%;
    padding: 0%;
    overflow: auto;
}
.titer{
    font-size:45px;
    color: black;
    font-family:'vazir';
    padding: 10px;
    margin: 0;
}
.groupsconnect{
    display: flex;
    flex-direction: row;
    direction: rtl;
    width: 85%;
    background-color: rgba(219, 137, 50, 0.493);
    border-radius: 10px;
    margin: 10px;
    flex-wrap: wrap;
}
.groupsconnect1{
    height: 100%;
    margin: 0%;
    background-color: rgba(0, 0, 255, 0.482);
    width: 50px;
    border-top-right-radius: 10px;
    border-bottom-right-radius:10px ;
}
.groupsconnect2{
    font-size: 25px;
    padding:4px;
    font-weight: bold;
}
.groupsconnect3{
    display: flex;
    font-size: 18px;
    padding: 4px;
    flex-wrap: wrap;
}
.groupsconnect4{
    height: 100%;
}
@media(max-width: 800px){
    .dispconnect{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: 100%;
        margin: 0%;
        padding: 0%;
        overflow: auto;
    }
    .titer{
        font-size:20px;
        color: black;
        font-family:'vazir';
        padding: 10px;
        margin: 0;
    }
    .groupsconnect{
        display: flex;
        flex-direction: row;
        direction: rtl;
        width: 95%;
        background-color: rgba(219, 137, 50, 0.493);
        border-radius: 10px;
        margin: 5px;
        flex-wrap: wrap;
    }
    .groupsconnect1{
        height: 100%;
        margin: 0%;
        background-color: rgba(0, 0, 255, 0.482);
        width: 35px;
        border-top-right-radius: 10px;
        border-bottom-right-radius:10px ;
    }
    .groupsconnect2{
        font-size:15px;
        padding:4px;
        font-weight: bold;
    }
    .groupsconnect3{
        display: flex;
        font-size: 12px;
        padding: 4px;
        flex-wrap: wrap;
    }
    .groupsconnect4{
        height: 100%;
    }
}